import service from "../utils/request";
import qs from 'qs';

export function Ping(addr){
    return service({
        url: '/ping',
        method: 'post',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({
            addr: addr
        })
    })
}
export function Traceroute(addr){
    return service({
        url: '/traceroute',
        method: 'post',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({
            addr: addr
        })
    })
}
export function FTP(){
    return service({
        url: '/ftpcheck',
        method: 'get'
    })
}
export function Shell(shell){
    return service({
        url: '/shellcheck',
        method: 'post',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({
            shell: shell
        })
    })
}


